import React, { useState } from 'react'

import PropTypes from 'prop-types'

import './Select.styles.scss'
import { useSelector } from 'react-redux'
import { useDispatch } from 'react-redux'
import { switchPais } from '../../../store/slices/desAdv'
/**
 * @param name (String)
 * @param value (String)
 * @returns JSX
 */

const SelectRender = React.memo(({ data }) => {
  const [rowValue, setRowValue] = useState('')
  const editMode = useSelector((state) => state.desAdv.isEdit)
  const { id, name, value, type, valueBold } = data

  const dispatch = useDispatch()
  React.useEffect(() => {
    if (rowValue !== data.defaultValue) {
      setRowValue(data.defaultValue)
    }
  }, [data.defaultValue])

  const setRow = (e) => {
    if (e.target.value === rowValue) return

    if (id === 2 || id === 0) {
      console.log(id, value)
      let newData = { ...data, defaultValue: e.target.value }
      setRowValue(e.target.value)
      dispatch(switchPais({ newData }))
    }
  }

  return editMode ? (
    <select value={rowValue} onChange={setRow}>
      {data.value.length &&
        data.value.map((v) => <option value={v}>{v}</option>)}
    </select>
  ) : (
    <div>{data.defaultValue}</div>
  )
})

SelectRender.propTypes = {
  name: PropTypes.string,
  data: PropTypes.object,
}

SelectRender.defaultProps = {
  name: 'select',
  data: {
    id: null,
    name: '',
    value: ['0', '10', '18', '20'],
    type: 'select',
    valueBold: false,
    defaultValue: '18',
  },
}

export default SelectRender
