import React, { useCallback, useState } from 'react'
import { useDispatch } from 'react-redux'
import { setPais, setNDS } from '../../../store/slices/desAdv'

import PropTypes from 'prop-types'

import './LineBlock.styles.scss'
import SelectRender from '../Select/Select'

/**
 * @param name (String)
 * @param value (String)
 * @returns JSX
 */
const LineBlock = React.memo(({ data }) => {
  const [stateV, setstateV] = useState(data.value)
  const { id, name, value, type, valueBold } = data

  const dispatch = useDispatch()

  const setPaisHandler = useCallback(() => {
    if (id === 3) {
      console.log(id)
      dispatch(setPais({ id }))
    } else if (id === 1) {
      console.log(id)
      dispatch(setNDS({ id }))
    }
  }, [data])

  return (
    <div className="line-block">
      {name && <div className="line-block_key">{name}:</div>}
      {type === 'div' && (
        <div className={valueBold ? 'bold' : 'line-block_value'}>{value}</div>
      )}
      {type === 'text' && (
        <input
          type="text"
          value={stateV}
          onChange={(e) => setstateV(e.target.value)}
        />
      )}
      {type === 'date' && (
        <input
          type="date"
          value={stateV}
          onChange={(e) => setstateV(e.target.value)}
        />
      )}
      {type === 'select' && <SelectRender data={data} />}
      {type === 'button' && (
        <input onClick={setPaisHandler} type="button" value={value} />
      )}
    </div>
  )
})

LineBlock.propTypes = {
  name: PropTypes.string,
  data: PropTypes.object,
}

LineBlock.defaultProps = {
  name: 'Название(1)',
  data: {
    name: 'Номер заказа',
    value: 'regress2506-2',
    type: 'div',
    valueBold: false,
  },
}

export default LineBlock
