const desAdvSlice = createSlice({
  name: 'desAdv',
  initialState,
  reducers: {
    setEditMode(state, action) {
      state.isEdit = action.payload.isEdit
    },
    setPais(state, action) {
      console.log('pais', action.payload)
      const pais = state.dataDesAdvTools[2].defaultValue
      state.dataDesBlockTable.data.map((el) => {
        if (el.OriginCountry !== pais) {
          el.OriginCountry = pais
        }
      })
    },
    setNDS(state, action) {
      console.log('setNDS', action.payload)
      const nds = state.dataDesAdvTools[0].defaultValue
      state.dataDesBlockTable.data.map((el) => {
        if (el.Vat !== nds) {
          el.Vat = nds
        }
      })
    },
    switchPais(state, action) {
      console.log('switchPais', action.payload.newData)
      if (action.payload.newData.id === 2) {
        // const pais = state.dataDesAdvTools[2].defaultValue
        state.dataDesAdvTools[2] = action.payload.newData
      } else if (action.payload.newData.id === 0) {
        // const pais = state.dataDesAdvTools[2].defaultValue
        state.dataDesAdvTools[0] = action.payload.newData
      }
      // state.dataDesAdvTools = action.payload.isEdit
    },
    switchQty(state, action) {
      /**
       * Qty in
       * NetAmount change
       * GrossAmount change
       * TaxAmount change
       * В общей таблице
       * Количество по накладной
       * Сумма без НДС всего
       * Сумма с НДС всего
       */
      console.log('switchQty', action.payload.newData)
      if (!action.payload.newData) {
        return
      }
      if (!action.payload.newData.value) {
        return
      }
      let id = action.payload.newData.id
      let valueQty = parseFloat(action.payload.newData.value)
      const inx = state.dataDesBlockTable.data.findIndex((el) => el.id === id)
      const qty = typeof valueQty === 'string' ? parseInt(valueQty) : valueQty
      // console.log({ Qty })
      const value = state.dataDesBlockTable.data[inx].Price1
      // const netAmount = qty * price1

      // const netVat = parseInt(state.dataDesBlockTable.data[inx].Vat)
      // let grossAmount = netAmount.toFixed(2) + netVat

      const newData = {
        ...state.dataDesBlockTable.data[inx],
        Qty: qty.toFixed(3),
        // NetAmount: netAmount,
        // GrossAmount: grossAmount,
      }

      state.dataDesBlockTable.data[inx] = newData
    },
    setPrice(state, action) {
      /**
       * Price1 in tabLine
       * Price2 change (Цена с НДС)
       * NetAmount change (Сумма без НДС)
       * TaxAmount change
       * GrossAmount change (Сумма с НДС)
       *
       * В общей таблице
       * Сумма без НДС всего NetAmount
       * Сумма с НДС всего Vat
       * НДС всего Vat
       */
      console.log('setPrice', action.payload.newData)
      if (!action.payload.newData) {
        return
      }

      let id = action.payload.newData.id

      let value = action.payload.newData.value
      const inx = state.dataDesBlockTable.data.findIndex((el) => el.id === id)

      if (action.payload.newData.value === '-1') {
        value = state.dataDesBlockTable.data[inx].Price1
      }

      const price1 = parseFloat(value) //6.00
      // console.log({ price1 })
      /**
       * count Price2 = vat(НДС) + price1
       */
      const vat = parseInt(state.dataDesBlockTable.data[inx].Vat)
      let price2 = (vat * price1) / 100
      price2 += price1

      /**
       * count NetAmount = Qty(количество) * price1
       */
      const qty = parseInt(state.dataDesBlockTable.data[inx].Qty)
      const netAmount = qty * price1

      /**
       * TaxAmount
       */
      let taxAmount = (netAmount / 100) * vat

      /**
       * GrossAmount = netAmount + netAmount/100 * Vat(НДС)
       */

      let grossAmount = (netAmount / 100) * vat
      grossAmount += netAmount
      /**
       * Сумма без НДС всего(TabSumm) = netAmount все из выборки
       */

      let netAmountAll = netAmount
      state.dataDesBlockTable.data.forEach((el, index) => {
        if (index !== inx) {
          netAmountAll += parseFloat(el.NetAmount)
        }
      })

      /**
       * Сумма  НДС всего = taxAmount все из выборки
       */

      let taxAmountAll = taxAmount
      state.dataDesBlockTable.data.forEach((el, index) => {
        if (index !== inx) {
          taxAmountAll += parseFloat(el.TaxAmount)
        }
      })
      /**
       * Сумма c НДС всего = grossAmount все из выборки
       */
      let grossAmountAll = grossAmount
      state.dataDesBlockTable.data.forEach((el, index) => {
        if (index !== inx) {
          grossAmountAll += parseFloat(el.GrossAmount)
        }
      })

      const newData = {
        ...state.dataDesBlockTable.data[inx],
        Price2: formatData(price2),
        NetAmount: formatData(netAmount),
        GrossAmount: formatData(grossAmount),
        TaxAmount: formatData(taxAmount),
      }
      state.dataDesBlockTable.data[inx] = newData

      let newSumm = {
        ...state.tabSumm,
        value: formatData(netAmountAll),
      }
      state.tabSumm = newSumm
      let newGross = {
        ...state.tabSummWithNds,
        value: formatData(grossAmountAll),
      }
      state.tabSummWithNds = newGross

      let newVatAll = {
        ...state.tabSummNds,
        value: formatData(taxAmountAll),
      }
      state.tabSummNds = newVatAll
    },
    switchTabLineSelect(state, action) {
      console.log('switchTabLineSelect', action.payload.newData)
      /**
       * Vat in tabLine
       * Price2 change (Цена с НДС)
       * NetAmount change (Сумма без НДС)
       * TaxAmount change
       * GrossAmount change (Сумма с НДС)
       *
       * В общей таблице
       * Сумма без НДС всего NetAmount
       * Сумма с НДС всего Vat
       * НДС всего Vat
       */
      if (!action.payload.newData) {
        return
      }
      if (!action.payload.newData.vat) {
        return
      }
      let id = action.payload.newData.id
      let value = action.payload.newData.vat
      const inx = state.dataDesBlockTable.data.findIndex((el) => el.id === id)
      const vat = parseFloat(value) //6.00
      // console.log({ price1 })
      /**
       * count Price2 = vat(НДС) + price1
       */
      const price1 = parseInt(state.dataDesBlockTable.data[inx].Price1)
      let price2 = (vat * price1) / 100
      price2 += price1

      /**
       * count NetAmount = Qty(количество) * price1
       */
      const qty = parseInt(state.dataDesBlockTable.data[inx].Qty)
      const netAmount = qty * price1

      /**
       * TaxAmount
       */
      let taxAmount = (netAmount / 100) * vat

      /**
       * GrossAmount = netAmount + netAmount/100 * Vat(НДС)
       */

      let grossAmount = (netAmount / 100) * vat
      grossAmount += netAmount
      /**
       * Сумма без НДС всего(TabSumm) = netAmount все из выборки
       */

      let netAmountAll = netAmount
      state.dataDesBlockTable.data.forEach((el, index) => {
        if (index !== inx) {
          netAmountAll += parseFloat(el.NetAmount)
        }
      })

      /**
       * Сумма  НДС всего = taxAmount все из выборки
       */

      let taxAmountAll = taxAmount
      state.dataDesBlockTable.data.forEach((el, index) => {
        if (index !== inx) {
          taxAmountAll += parseFloat(el.TaxAmount)
        }
      })
      /**
       * Сумма c НДС всего = grossAmount все из выборки
       */
      let grossAmountAll = grossAmount
      state.dataDesBlockTable.data.forEach((el, index) => {
        if (index !== inx) {
          grossAmountAll += parseFloat(el.GrossAmount)
        }
      })

      const newData = {
        ...state.dataDesBlockTable.data[inx],
        Price2: formatData(price2),
        NetAmount: formatData(netAmount),
        GrossAmount: formatData(grossAmount),
        TaxAmount: formatData(taxAmount),
      }
      state.dataDesBlockTable.data[inx] = newData

      let newSumm = {
        ...state.tabSumm,
        value: formatData(netAmountAll),
      }
      state.tabSumm = newSumm
      let newGross = {
        ...state.tabSummWithNds,
        value: formatData(grossAmountAll),
      }
      state.tabSummWithNds = newGross

      let newVatAll = {
        ...state.tabSummNds,
        value: formatData(taxAmountAll),
      }
      state.tabSummNds = newVatAll
    },
  },
})

export const {
  setEditMode,
  setPais,
  switchPais,
  setNDS,
  switchTabLineSelect,
  switchQty,
  setPrice,
} = desAdvSlice.actions
export default desAdvSlice.reducer

function formatData(arg) {
  if (typeof arg === 'number') {
    return arg.toFixed(2)
  } else if (typeof arg === 'string') {
    if (arg.includes('.')) {
      let tmpArr = arg.split('.')
      return tmpArr[0] + '.' + tmpArr[1].slice(2)
    } else {
      return arg + '.00'
    }
  }
}
