import React, { useState } from 'react'
import { useSelector } from 'react-redux'

import PropTypes from 'prop-types'

import './DesBlockTable.styles.scss'
import LineBlock from '../../../lib/components/LineBlock'
import RowLineTab from './RowLineTab'

const DesBlockTable = React.memo(({ editMode }) => {
  const {
    dataDesBlockTable,
    tabAmountLine,
    tabDesAmount,
    tabSumm,
    tabSummWithNds,
    tabSummNds,
  } = useSelector((state) => state.desAdv)

  return (
    <div>
      <div className="tab-block">
        <table className="tcnt" cellspacing="4" border="1">
          <thead>
            {Object.keys(dataDesBlockTable.header).length && (
              <RowLineTab
                isHeader
                values={Object.values(dataDesBlockTable.header)}
              />
            )}
          </thead>
          <tbody>
            {dataDesBlockTable.data.length &&
              dataDesBlockTable.data.map((values, index) => (
                <RowLineTab
                  isHeader={false}
                  key={values.id + values.EAN}
                  values={values}
                  odd={index}
                />
              ))}
          </tbody>
        </table>
      </div>
      <div className="tab-summ">
        <LineBlock data={tabAmountLine} />
        <LineBlock data={tabDesAmount} />
        <LineBlock data={tabSumm} />
        <LineBlock data={tabSummWithNds} />
        <LineBlock data={tabSummNds} />
      </div>
    </div>
  )
})

