import React from 'react'

import PropTypes from 'prop-types'

import './InfoSingleBlock.styles.scss'
import LineBlock from '../LineBlock'

/**
 * @param title (String)
 * @param data (String[])
 * @returns JSX
 */
const InfoSingleBlock = ({ title, data }) => {
  return (
    <>
      <div className="info-single-block">
        <div className="title-info-block">{title}</div>
        {data.length && data.map((data) => <LineBlock data={data} />)}
      </div>
    </>
  )
}

InfoSingleBlock.propTypes = {
  title: PropTypes.string,
  data: PropTypes.array,
}

InfoSingleBlock.defaultProps = {
  title: 'Поставщик*',
  data: [
    {
      name: 'Название*',
      value: 'ООО “Семь + Я” поставщик',
      type: 'div',
      valueBold: false,
    },
    {
      name: 'Адрес*',
      value: 'Нет данных',
      type: 'div',
      valueBold: false,
    },
    {
      name: 'GLN*',
      value: '2000000009972',
      type: 'div',
      valueBold: true,
    },
    {
      name: 'Код поставщика',
      value: '888*',
      type: 'div',
      valueBold: true,
    },
  ],
}
export default InfoSingleBlock
