import React from 'react'
import { useSelector } from 'react-redux'
import LineBlock from '../../../lib/components/LineBlock'

// import PropTypes from 'prop-types'

import './DesAdvTools.styles.scss'

const DesAdvTools = () => {
  const { dataDesAdvTools } = useSelector((state) => state.desAdv)

  // const setPais = (value) => {
  //   console.log(value)
  // }
  return (
    <div className="des-adv-tools-block">
      <div className="left">
        {dataDesAdvTools.length &&
          dataDesAdvTools
            .slice(0, 4)
            .map((data) => <LineBlock key={data.id} data={data} />)}
      </div>
      <div className="right">
        {dataDesAdvTools.length &&
          dataDesAdvTools
            .slice(4, 7)
            .map((data) => <LineBlock key={data.id} data={data} />)}
      </div>
    </div>
  )
}

// DesAdvTools.propTypes = {
//   value: PropTypes.string,
// }

// DesAdvTools.defaultProps = {
