import React, { useEffect } from 'react'

import PropTypes from 'prop-types'

// import xml2json from '../../api/utils/xml2json'
import XMLData from '../../api/Utkonos/desadv.xml'
import XMLParser from 'react-xml-parser'

import './desadv.styles.scss'
import Header from '../../lib/components/Header'
import Title from '../../lib/components/Title-page'
import InfoBlock from './InfoBlock/InfoBlock'
import DesBlock from './DesBlock/DesBlock'
import DesBlockTable from './DesBlockTable/DesBlockTable'
import axios from 'axios'
import DesAdvTools from './DesAdvTools/DesAdvTools'

const DesAdv = () => {
  useEffect(() => {
    axios
      .get(XMLData, {
        'Content-Type': 'application/xml; charset=utf-8',
      })
      .then((response) => {
        var jsonDataFromXml = new XMLParser().parseFromString(
          response.data,
          'text/xml',
        )
        // var jsonDataFromXml = xml2json(response.data)
        let jsonData = jsonDataFromXml.children
        console.log(jsonData)
      })
  }, [])

  return (
    <>
      <Header />
      <Title title="Накладная" />
      <InfoBlock title="Точка доставки" />
      <DesBlock />
      <DesAdvTools />
      <DesBlockTable />
    </>
  )
}

DesAdv.propTypes = {
  // children: PropTypes.node,
  // handleChangeStage: PropTypes.func.isRequired,
  // onFormSubmit: PropTypes.func.isRequired,
  value: PropTypes.string,
}
DesAdv.defaultProps = {
  // children: null,
  // handleChangeStage: () => {},
  // onFormSubmit: () => {},
  value: '',
}

export default DesAdv
